import init, * as bead from "bead";
import { InputEvent } from "./events";

// Is this a good thing to do?
init().then(() => {
    bead.bead_init();
    setup();
});

let timer: NodeJS.Timeout | null = null;


function execute(context: bead.Context) {
    if (!context.has_change()) {
        return;
    }

    context.process();
    // We need to create a serialized/native JS object we can send as a web worker message
    const pattern = context.get_pattern();
    if (pattern) {
        postMessage({
            name: "NewPattern",
            pattern: pattern.to_json()
        });
    }

    timer = null;
}

function setup() {
    let context = bead.Context.new();

    onmessage = (e: MessageEvent<InputEvent>) => {
        const event = e.data;
        // console.log("Worker got event", event);

        switch (event.name) {
            case "SetSize":
                context.set_size(event.kind, event.value);
                break;

            case "SetImage":
                context.set_image(event.image);
                break;

            case "SetProfile":
                const profile = bead.ColorProfile.from_json(event.profile);
                if (profile) {
                    context.set_profile(profile);
                }
                break;

            case "SetColorSettings":
                const settings = bead.ColorSettings.new(event.settings.brightness, event.settings.contrast);
                context.set_color_settings(settings);
        }

        if (context.has_change() && (timer == null)) {
            timer = setTimeout(() => execute(context), 5);
        }

    };
}